	JudeGoodsOwned:function(Id){
		for (var i = this.GoodsList.length - 1; i >= 0; i--) {
			if(this.GoodsList[i].Id == Id && this.GoodsList[i].ownNum > 0)
			{
				return true;
			}
		}
		return false;
	},

